import '@babel/runtime/regenerator';

import WasmWrapper from './wasm-wrapper';
import _Thread from './threads/thread';
import _Thread2 from './threads/thread2';
import _Thread3 from './threads/thread3';



const IDLE_FUNCTION = () => 0;



export class PromiseBunch
{
	constructor ()
	{
		this.running_count = 0;
	}

	run (callback1, callback2 = IDLE_FUNCTION)
	{
		++this.running_count;

		if (this.running_count > 1)
		{
			this.callback_next1 = callback1;
			this.callback_next2 = callback2;

			return false;
		}

		return Promise.all(callback1())
			.then
			(
				resolves =>
				{
					callback2(resolves);

					if (this.running_count > 1)
					{
						this.running_count = 0;

						return this.run(this.callback_next1, this.callback_next2);
					}

					this.running_count = 0;

					return true;
				}
			);
	}

	async runAsync (callback1, callback2 = IDLE_FUNCTION)
	{
		++this.running_count;

		if (this.running_count > 1)
		{
			this.callback_next1 = callback1;
			this.callback_next2 = callback2;

			return false;
		}

		return Promise.all(await callback1())
			.then
			(
				resolves =>
				{
					callback2(resolves);

					if (this.running_count > 1)
					{
						this.running_count = 0;

						return this.runAsync(this.callback_next1, this.callback_next2);
					}

					this.running_count = 0;

					return true;
				}
			);
	}

	/* eslint-disable-next-line class-methods-use-this */
	run2 (callback1, callback2 = IDLE_FUNCTION)
	{
		return Promise.all(callback1()).then(callback2);
	}

	/* eslint-disable-next-line class-methods-use-this */
	async runAsync2 (callback1, callback2 = IDLE_FUNCTION)
	{
		return Promise.all(await callback1()).then(callback2);
	}
}



Object.assign
(
	WasmWrapper,

	{
		Thread: _Thread,
		Thread2: _Thread2,
		Thread3: _Thread3,
	},
);



export const Thread = _Thread;
export const Thread2 = _Thread2;
export const Thread3 = _Thread3;



export default WasmWrapper;
