export default function ()
{
	const that = this;



	this.options =
		Object.assign
		(
			{
				// 1024 b * 1024 b == 1048576 b == 1 mb
				thread_stack_size: 0x0100000,
			},

			this.options,
		);



	[
		'Uint8Array',
		'Int8Array',
		'Uint16Array',
		'Int16Array',
		'Uint32Array',
		'Int32Array',
		'Uint8Array',
		'Int8Array',
		'BigUint64Array',
		'BigInt64Array',
		'Float32Array',
		'Float64Array',
	]
		.forEach
		(
			typed_array_name =>
			{
				if (self[typed_array_name])
				{
					this[typed_array_name] =
						class extends self[typed_array_name]
						{
							constructor (...args)
							{
								// if (args.length !== 1)
								// {
								// 	throw new Error('wasm-wrapper typed array must be constructed with only 1 argument.');
								// }

								if (args[0] instanceof self.ArrayBuffer || (self.SharedArrayBuffer && args[0] instanceof self.SharedArrayBuffer))
								{
									if (args[0] === that.memory.buffer)
									{
										super(...args);

										return;
									}

									throw new Error('wasm-wrapper typed array can not be backed by array buffer other than wasm-wrapper instance memory buffer.');
								}

								let length = 0;

								if (typeof args[0] === 'object')
								{
									// length = args[0].length;
									({ length } = args[0]);
								}
								else
								{
									length = args[0];
								}

								const byteOffset = that.exports.__malloc(length * self[typed_array_name].BYTES_PER_ELEMENT);

								super(that.memory.buffer, byteOffset, length);

								if (typeof args[0] === 'object')
								{
									this.set(args[0]);
								}
							}

							free () { that.exports.__free(this.byteOffset); }

							// slice (...args) {}
						};
				}
			},
		);



	{
		this.mem =
		{
			'bool': { kind: 'Uint' },
			'char': { kind: 'Uint' },
			'int': { kind: 'Int' },
			'short': { kind: 'Int' },
			'long': { kind: 'Int' },
			'size_t': { kind: 'Uint' },
			'addr': { kind: 'Uint' },
			'uint8_t': { kind: 'Uint' },
			'int8_t': { kind: 'Int' },
			'uint16_t': { kind: 'Uint' },
			'int16_t': { kind: 'Int' },
			'uint32_t': { kind: 'Uint' },
			'int32_t': { kind: 'Int' },
			'uint64_t': { kind: 'Uint' },
			'int64_t': { kind: 'Int' },
			'float': { kind: 'Float' },
			'double': { kind: 'Float' },
			'long_double': { kind: 'Float' },
			'c_string': {},
		};

		this.type_names = Object.keys(this.mem);

		this.type_names
			.forEach
			(
				(type_name, type_name_index) =>
				{
					const size = this.exports.__sizeof(type_name_index);

					let typed_array_constructor_name = `${ this.mem[type_name].kind }${ size * 8 }Array`;

					if (!this[typed_array_constructor_name])
					{
						typed_array_constructor_name = `Big${ typed_array_constructor_name }`;
					}

					Object.assign
					(
						this.mem[type_name],

						{
							size,
							typed_array_constructor: this[typed_array_constructor_name],
							interp_log: addr => this[type_name](addr)[0],
							interp_thread: addr => this[type_name](addr),
						},
					);
				},
			);

		this.mem.bool.interp_log = addr => Boolean(this.bool(addr)[0]),

		this.mem.c_string.interp_log = addr => this.c_string2(this.addr(addr)[0]);
		this.mem.c_string.interp_thread = addr => this.addr(addr);
		this.mem.c_string.interp_arg = str => new this.mem.char.typed_array_constructor(this.constructor.convertDomStringToUint8Array(str));
	}



	{
		this.memory_views = {};

		this.memory_views.UI8 = new this.Uint8Array(this.memory.buffer);
		// this.memory_views.I8 = new this.Int8Array(this.memory.buffer);
		// this.memory_views.UI16 = new this.Uint16Array(this.memory.buffer);
		// this.memory_views.I16 = new this.Int16Array(this.memory.buffer);
		// this.memory_views.UI32 = new this.Uint32Array(this.memory.buffer);
		this.memory_views.I32 = new this.Int32Array(this.memory.buffer);
		// if (this.BigUint64Array) { this.memory_views.UI64 = new this.BigUint64Array(this.memory.buffer); }
		// if (this.BigInt64Array) { this.memory_views.I64 = new this.BigInt64Array(this.memory.buffer); }
		// this.memory_views.F32 = new this.Float32Array(this.memory.buffer);
		// this.memory_views.F64 = new this.Float64Array(this.memory.buffer);
	}
}
