/**
 * Using TypedArray.subarray() is preferred
 * when accessing to data
 * to avoid extra memory allocation.
 *
 * Strange std::string behavior:
 * if std::string data length <=11, std::string object address is the same with its data;
 * if >11, std::string object name stores address of beginning of the data.
 * So in second case one can use WasmWrapper::c_string method to get string bytes.
 * Maybe it's not related to data length, but to dynamic memory allocation.
 *
 *
 *
 * If memory is shared then memory growing is not allowed
 * and maximum memory size is specified at compilation.
 *
 *
 *
 * Passing argument by reference works as passing by pointer.
 * So, functions with reference parameters expect address instead of value.
 *
 *
 *
 * TODO: determination capabiity of what wasm memory type is being used.
 */



// Webpack arraybuffer-loader is required.
// import test_simd_wasm_buffer from './test-simd/build/clang-wasm32/output/wasm/test-simd.wasm';

import configureMemory from './memory';
import getImports from './imports';
import cxx_specific_functions from './cxx-specific';



const __CODE_TYPE_BYTECODE__ = 0;
const __CODE_TYPE_FUNCTION__ = 1;



const IDLE_FUNCTION = () => 0;

const DEBUG_LOG = console.log.bind(console);
// const DEBUG_LOG = (...content) => console.log(...content.map(elm => `%c${ elm }`), 'background-color: #00858A; padding: 2px;');
const ERROR_LOG = content => console.log(`%c${ content }`, 'background-color: red; padding: 2px;');
const MISSED_IMPORT_FUNCTION_LOG = ERROR_LOG;

const DEBUG_INFO = [];

const DEBUG_INFO_PUSH = content => DEBUG_INFO.push(content);

const DEBUG_LABEL_LOG = () =>
{
	DEBUG_INFO.forEach(label => console.log(`%c${ label }`, 'background-color: #00858A; padding: 2px;'));

	DEBUG_INFO.length = 0;
};



DEBUG_INFO_PUSH(`WASM CONCURRENCY: ${ self.navigator.hardwareConcurrency }`);



class WasmWrapper
{
	static text_decoder = new TextDecoder('utf-8');
	static text_encoder = new TextEncoder();

	// static convertUint8ArrayToDomString (uint8_array)
	// {
	// 	return WasmWrapper.text_decoder.decode(uint8_array);
	// }

	// Version for shared buffer backed array.
	// Decoding views of shared buffer is not allowed.
	static convertUint8ArrayToDomString (uint8_array)
	{
		// return WasmWrapper.text_decoder.decode(uint8_array.slice());
		return WasmWrapper.text_decoder.decode(new Uint8Array(uint8_array));
	}

	static convertDomStringToUint8Array (text)
	{
		return WasmWrapper.text_encoder.encode(text);
	}

	static async testSimd ()
	{
		let result = false;

		try
		{
			// webpack arraybuffer-loader has to be installed when using the library with soures files.
			const test_simd_wasm_buffer = (await import('./test-simd/build/clang-wasm32/output/wasm/test-simd.wasm')).default;

			const wasm_module = await WebAssembly.compile(test_simd_wasm_buffer);

			// LOG(WebAssembly.Module.exports(wasm_module))
			// LOG(WebAssembly.Module.imports(wasm_module))

			await WebAssembly.instantiate
			(
				wasm_module,

				{
					env:
					{
						memory:
						(
							new WebAssembly.Memory
							({
								initial: 2,
								maximum: 2,
							})
						),
					},
				},
			);

			// // Webpack since version 4 has embedded WASM parser.
			// // MIME type "application/wasm" must be defined for ".wasm" extension on server.
			// // TODO: use another type of importing ?
			// await import('./test-simd/build/clang-wasm32/output/wasm/test-simd.wasm');

			result = true;
		}
		catch (error)
		{
			ERROR_LOG(`WASM SIMD TEST FAILED: ${ error }`);
		}

		DEBUG_INFO_PUSH(`WASM SIMD: ${ result ? 'ON' : 'OFF' }`);

		return result;
	}

	constructor (options = {})
	{
		self.__wasm_wrapper__ = this;

		this.options = options;

		const wasm_wrapper = this;



		class ThreadPromiseBunch
		{
			constructor ()
			{
				this.running_count = 0;
			}

			run (callback1, callback2 = IDLE_FUNCTION)
			{
				++this.running_count;

				if (this.running_count > 1)
				{
					this.callback_next1 = callback1;
					this.callback_next2 = callback2;

					return false;
				}

				wasm_wrapper.exports.__initHeapPointer();

				return Promise.all(callback1())
					.then
					(
						resolves =>
						{
							wasm_wrapper.exports.__resetHeapPointer();

							callback2(resolves);

							if (this.running_count > 1)
							{
								this.running_count = 0;

								return this.run(this.callback_next1, this.callback_next2);
							}

							this.running_count = 0;

							return true;
						}
					);
			}

			runLog (callback1, callback2 = IDLE_FUNCTION)
			{
				++this.running_count;

				if (this.running_count > 1)
				{
					this.callback_next1 = callback1;
					this.callback_next2 = callback2;

					return false;
				}

				wasm_wrapper.exports.__initHeapPointer();

				LOG('ThreadPromiseBunch start __heap_pointer', wasm_wrapper.addr(wasm_wrapper.globals.__heap_pointer)[0])

				return Promise.all(callback1())
					.then
					(
						resolves =>
						{
							LOG('ThreadPromiseBunch end __heap_pointer', wasm_wrapper.addr(wasm_wrapper.globals.__heap_pointer)[0])

							wasm_wrapper.exports.__resetHeapPointer();

							callback2(resolves);

							if (this.running_count > 1)
							{
								this.running_count = 0;

								return this.run(this.callback_next1, this.callback_next2);
							}

							this.running_count = 0;

							return true;
						}
					);
			}

			async runAsync (callback1, callback2 = IDLE_FUNCTION)
			{
				++this.running_count;

				if (this.running_count > 1)
				{
					this.callback_next1 = callback1;
					this.callback_next2 = callback2;

					return false;
				}

				wasm_wrapper.exports.__initHeapPointer();

				return Promise.all(await callback1())
					.then
					(
						resolves =>
						{
							wasm_wrapper.exports.__resetHeapPointer();

							callback2(resolves);

							if (this.running_count > 1)
							{
								this.running_count = 0;

								return this.runAsync(this.callback_next1, this.callback_next2);
							}

							this.running_count = 0;

							return true;
						}
					);
			}

			run2 (callback1, callback2 = IDLE_FUNCTION)
			{
				wasm_wrapper.exports.__initHeapPointer();

				return Promise.all(callback1())
					.then
					(() =>
					{
						wasm_wrapper.exports.__resetHeapPointer();

						callback2();
					});
			}

			async runAsync2 (callback1, callback2 = IDLE_FUNCTION)
			{
				wasm_wrapper.exports.__initHeapPointer();

				return Promise.all(await callback1())
					.then
					(() =>
					{
						wasm_wrapper.exports.__resetHeapPointer();

						callback2();
					});
			}
		}

		this.ThreadPromiseBunch = ThreadPromiseBunch;



		// configureMemory(this, options);
	}

	function (addr)
	{
		return this.exports.__indirect_function_table.get(addr);
	}

	async async (_function, ...args)
	{
		this.async_args = args;

		(this.async_func = _function)(...args);

		this.exports.asyncify_stop_unwind();

		await this.async_promise;

		this.async_count = 0;
	}

	async instantiate ({ wasm_module_imports, imports_lib, imports_custom, wasm_module, code, debug })
	{
		// Object for passing to threads.
		this.imports_thread = { env: {} };

		Object.keys(imports_custom)
			.forEach
			(
				module =>
				{
					Object.keys(imports_custom[module])
						.forEach
						(
							import_custom =>
							{
								if (!this.imports_thread[module])
								{
									this.imports_thread[module] = {};
								}

								this.imports_thread[module][import_custom] = 0;

								// TODO: use "|=" operator ?
								// If (imports_custom[module][import_custom] === 0) then make it IDLE_FUNCTION.
								imports_custom[module][import_custom] = imports_custom[module][import_custom] || IDLE_FUNCTION;
							}
						);
				},
			);

		const imports_aggregate = {};

		Object.assign(imports_aggregate, imports_custom);

		Object.assign
		(
			imports_aggregate.env,

			imports_lib
		);

		// Imported memory
		if (this.memory)
		{
			imports_aggregate.env.memory = this.memory;
		}

		wasm_module_imports
			.forEach
			(
				wasm_module_import =>
				{
					if (!imports_aggregate[wasm_module_import.module])
					{
						imports_aggregate[wasm_module_import.module] = {};
					}

					if (!imports_aggregate[wasm_module_import.module][wasm_module_import.name])
					{
						if (debug)
						{
							DEBUG_LOG(`Missed ${ wasm_module_import.module } import:`, wasm_module_import.name);
						}

						imports_aggregate[wasm_module_import.module][wasm_module_import.name] = IDLE_FUNCTION;
						// imports_aggregate[wasm_module_import.module][wasm_module_import.name] = () => { MISSED_IMPORT_FUNCTION_LOG(`Missed import function called: module "${ wasm_module_import.module }", function "${ wasm_module_import.name }"`); };

						// if (wasm_module_import.name !== 'clock_time_get')
						// {
						// 	imports_aggregate[wasm_module_import.module][wasm_module_import.name] = () => { MISSED_IMPORT_FUNCTION_LOG(`Missed import function called: module "${ wasm_module_import.module }", function "${ wasm_module_import.name }"`); };
						// }

						// if (wasm_module_import.name === 'fd_write')
						// {
						// 	imports_aggregate[wasm_module_import.module][wasm_module_import.name] = (...args) => { MISSED_IMPORT_FUNCTION_LOG(`Missed import function called: module "${ wasm_module_import.module }", function "${ wasm_module_import.name }"`); LOG(args.map(arg => this.CString2(arg))); };
						// }
					}
				},
			);

		let exports = null;

		if (wasm_module)
		{
			const wasm_module_instance =
				await WebAssembly.instantiate
				// await WebAssembly.instantiateStreaming
				(
					wasm_module,

					imports_aggregate,
				);

			if (debug)
			{
				DEBUG_LOG(wasm_module_instance);
			}

			// this.instance = wasm_module_instance;

			({ exports } = wasm_module_instance);
		}
		else
		{
			// imports_aggregate.wasi_snapshot_preview1 = {};
			// imports_aggregate.wasi_snapshot_preview1.fd_write = (...args) => LOG(args.map(arg => this.c_string2(arg)));

			exports = code(imports_aggregate);
		}

		// Exported memory
		if (!this.memory)
		{
			this.memory = exports.memory;
		}

		return exports;
	}

	async init
	({
		// TODO: reorder.
		code,
		memory = null,
		imports: imports_custom = { env: {} },
		stack_pointer = undefined,
		configureMemory = true,
		forceLocalMemory = false,
		memory_params = { minimum: 65536, maximum: 65536, initial: 65536, shared: true },
		initGlobals = true,
		demangleCxxNames = false,
		debug = false,
		findOptimalMemorySize = true,
	})
	{
		let code_type = null;

		// Code for passing to other WASM instances (e.g. threads).
		this.code = code;

		if (typeof code === 'string')
		{
			// if (code.slice(0, 8) === 'function')
			if (code.startsWith('function'))
			{
				code_type = __CODE_TYPE_FUNCTION__;

				code = new Function(code);

				DEBUG_INFO_PUSH(`WASM CODE: string function -> ${ code.constructor.name }`);
			}
			// base64
			else
			{
				code = Uint8Array.from(atob(code), c => c.charCodeAt(0));

				// TODO: Make code shared (if can) to speed up passing it to workers. DONE?
				if (self.SharedArrayBuffer)
				{
					const code_temp = code;

					code = new Uint8Array(new SharedArrayBuffer(code.length));

					code.set(code_temp);
				}

				this.code = code;

				code_type = __CODE_TYPE_BYTECODE__;

				DEBUG_INFO_PUSH(`WASM CODE: string base64 -> ${ code.constructor.name }`);
			}
		}
		else if (typeof code === 'function')
		{
			code_type = __CODE_TYPE_FUNCTION__;

			this.code = code.toString();

			DEBUG_INFO_PUSH(`WASM CODE: function`);
		}
		// Code is an Uint8Array backed (SharedArrayBuffer optionally) or ArrayBuffer or SharedArrayBuffer.
		// UPD. SharedArrayBuffer can not be used as source for WebAssembly module, only for Uint8Array
		// backed by SharedArrayBuffer.
		else
		{
			code_type = __CODE_TYPE_BYTECODE__;

			DEBUG_INFO_PUSH(`WASM CODE: ${ code.constructor.name }`);
		}

		DEBUG_INFO_PUSH(`WASM CODE: ${ code_type === __CODE_TYPE_BYTECODE__ ? 'BYTECODE' : 'FUNCTION' }`);

		// DEBUG_LABEL_LOG();



		const imports_lib = getImports(this);



		if (code_type === __CODE_TYPE_BYTECODE__)
		{
			const wasm_module = await WebAssembly.compile(code);

			if (debug)
			{
				DEBUG_LOG(wasm_module);
			}

			const wasm_module_imports = WebAssembly.Module.imports(wasm_module);
			// const wasm_module_exports = WebAssembly.Module.exports(wasm_module);

			this.memory = null;

			// Memory comes from WASM main worker instance to thread instance.
			// is always imported and shared.
			if (memory)
			{
				this.memory = memory;

				DEBUG_INFO_PUSH('WASM MEMORY: IMPORTED')
				DEBUG_INFO_PUSH('WASM MEMORY: SHARED')
			}
			else
			{
				const [ imported_memory ] =
					wasm_module_imports
						.filter((import_desc) => (import_desc.kind === 'memory'));

				const shared_memory_allowed = Boolean(self.SharedArrayBuffer) && !forceLocalMemory;

				if (imported_memory)
				{
					DEBUG_INFO_PUSH('WASM MEMORY: IMPORTED')

					let memory_desc = null;

					// chrome://flags#enable-experimental-webassembly-features must be enabled
					// to access "type" property of "imported_memory". It contains memory
					// type and size info produced by the linker (aka defined in WebAssembly module).
					// "--import-memory" linker flag specifies whether memory is imported to WebAssembly module.
					// "--shared-memory" linker flag specifies whether memory is shared between WebAssembly instances.
					// "--max-memory=N" linker flag specifies maximum size of memory for WebAssembly instance.
					if (imported_memory.type)
					{
						memory_desc =
						{
							// Is --initial-memory flag ignored if --shared-memory flag is defined?
							initial: imported_memory.type.maximum || imported_memory.type.minimum,
							maximum: imported_memory.type.maximum || imported_memory.type.minimum,
							shared: imported_memory.type.shared && shared_memory_allowed,
						};
					}
					// Parameters must match values produced by the linker (aka defined in WebAssembly module).
					else
					{
						memory_desc =
						{
							// 65536 pages == 4gb, maximum possible memory size for 32-bit WebAssembly instance.
							initial: memory_params.initial,
							maximum: memory_params.maximum, // Must match "--max-memory=N" flag.
							// shared, // Must match "--shared-memory" flag.
							shared: memory_params.shared && shared_memory_allowed
						};
					}

					this.memory = new WebAssembly.Memory(memory_desc);

					DEBUG_INFO_PUSH(`WASM MEMORY: ${ memory_desc.shared ? 'SHARED' : 'NOT SHARED' }`)
				}
				// TODO: add the case when memory is imported but can be created with arbitrary parameters.
				// Is this case possible? Meybe possible only when memory is not shared?
				else
				{
					DEBUG_INFO_PUSH('WASM MEMORY: EXPORTED')
				}
			}

			this.exports = await this.instantiate({ wasm_module_imports, imports_lib, imports_custom, wasm_module, debug });
		}
		/**
		 * Initializes WasmWrapper instance with JS code compiled from WASM with wasm2js.
		 *
		 * In this case presume WASM instances don't use shared memory and exported memory.
		 *
		 * wasm2js generates code with Atomics, even if its input WASM is compiled without flag "--shared-memory".
		 * TODO: use --disable-threads to generating Atomics as --help says.
		 * UPD: There are still Atomics even with --disable-threads flag.
		 * UPD: Removing "-matomics" compiler flag helped.
		 *
		 * if (!self.Atomics)
		 * {
		 *   self.Atomics = { load: (arr, index) => arr[index] };
		 * }
		 */
		else
		{
			this.memory = null;

			if (memory)
			{
				this.memory = memory;
			}
			else
			{
				const allocate =
					size =>
					{
						let result = true;

						try
						{
							this.memory = { buffer: new self.ArrayBuffer(size) };
						}
						catch (evt)
						{
							console.log(evt, size);

							result = false;
						}

						return result;
					};

				if (findOptimalMemorySize)
				{
					for (let size = memory_params.initial;; size /= 2)
					{
						if (allocate(size))
						{
							LOG(size, 'bytes allocated')

							break;
						}
					}
				}
				else
				{
					allocate(memory_params.initial);
				}
			}

			// TODO: Find better solution to get import modules used.
			// Now it is hardcoded.
			const wasm_module_imports =
			[
				{
					module: "env",
					module: "wasi_snapshot_preview1",
				},
			];

			this.exports = await this.instantiate({ wasm_module_imports, imports_lib, imports_custom, code, debug });
		}



		if (configureMemory)
		{
			this.configureMemory();

			DEBUG_LOG(this.mem);

			DEBUG_INFO_PUSH(`WASM MEMORY SIZE: ${ this.memory.buffer.byteLength }`);
			DEBUG_LABEL_LOG();
		}

		if (this.exports.__main)
		{
			this.__thread_id = this.exports.__main(stack_pointer);

			DEBUG_INFO_PUSH(`WASM THREAD: ${ this.__thread_id }`);
		}

		if (initGlobals)
		{
			this.globals = this.initGlobals();

			if (debug)
			{
				DEBUG_LOG(this.globals)
			}
		}

		if (demangleCxxNames)
		{
			const { exports_demangled, exports_demangled_reduced } = this.demangleCxxNames();

			Object.assign(this, { exports_demangled, exports_demangled_reduced });

			if (debug)
			{
				DEBUG_LOG(this.exports_demangled);
				DEBUG_LOG(this.exports_demangled_reduced);
			}
		}

		// this.__heap_pointer_initial = this.addr(this.globals.__heap_pointer)[0];
	}

	// resetHeapPointer ()
	// {
	// 	this.exports.__setHeapPointer(this.__heap_pointer_initial);
	// }

	initGlobals ()
	{
		const globals = {};

		const prefix_length = '__EXPORT_ADDR__'.length;

		Object.keys(this.exports)
			.forEach
			(
				export_name =>
				{
					if (export_name.startsWith('__EXPORT_ADDR__'))
					{
						globals[export_name.slice(prefix_length)] = this.exports[export_name]();
					}
				},
			);

		return globals;
	}

	getNewThread () { return new WasmWrapper.Thread(this); }
	getNewThread2 () { return new WasmWrapper.Thread2(this); }
	getNewThread3 () { return new WasmWrapper.Thread3(this); }

	async initThreads (thread_count)
	{
		const threads =
			new Array(thread_count)
				.fill(null)
				.map(() => new WasmWrapper.Thread3(this));

		await Promise.all(threads.map(thread => thread.init()));

		return threads;
	}

	terminateThreads (threads)
	{
		threads
			.reverse()
			.forEach(thread => thread.terminate());
	}
}



WasmWrapper.prototype.configureMemory = configureMemory;

Object.assign(WasmWrapper.prototype, cxx_specific_functions);



export default WasmWrapper;
